<template>
    <div>
        <van-nav-bar
                title="每天一个好心情"
                left-text="返回"
                left-arrow
                @click-left="onClickLeft"
                class="clock_in"
        />
        <van-field
                v-model="message"
                rows="2"
                autosize
                label=""
                type="textarea"
                maxlength="15"
                placeholder="写点儿什么吧..."
                show-word-limit
        />
        <div style="text-align: center;">
            <van-uploader v-model="fileList" multipart :max-count="1">
                <van-button icon="plus" type="primary">选择图片或拍照</van-button>
            </van-uploader>
        </div>
        <div style="width:280px;height:40px;margin:6px auto;" @click="submit">
            <van-button round block type="info" native-type="submit">
                提交
            </van-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Dayclockin",
        data() {
            return {
                fileList: [],
                message: '',
                integral:''
            }
        },
        watch: {
            fileList(val) {
                console.log(val)
            }
        },
        methods: {
            onClickLeft() {
                this.$router.push({
                    path: '/',
                    query:{active:2}
                })
            },
            submit() {
                var fileObj=this.fileList[0].file
                let email=this.$getCookie(' email')
                let message=this.message
                let params=new FormData()
                params.append('email',email)
                params.append('key','vv9544')
                params.append('content',message)
                params.append('mtspic',fileObj)
                let config={
                    headers:{'content-type':'multipart/form-data'}
                }
                this.$axios.post('https://zhuzuoji.com/myapi/moments/addmoments',params,config)
                    .then(res=>{
                        console.log(res)
                        if(res.data.state==1&&res.status==200){
                            this.$toast.success('打卡成功,积分+2')
                            this.$setCookie('integral',res.data.new_inte,1)
                            this.$router.push({
                                path:'/',
                                query:{
                                    active:2
                                }
                            })
                        }else {
                            this.$toast.fail('打卡失败')
                        }
                    })
                    .catch(err=>{
                        let error=err.toString()
                        if(error.indexOf('Network')!=-1){
                            this.$toast.fail('请检查网络')
                        }
                    })
            }
        },
    }
</script>

<style scoped>

    .clock_in {
        background-color: rgb(146,188,255);
    }

    .clock_input {
        width: 95%;
        height: auto;
        margin: 0 auto;
    }
    .clock_input > input {
        width: 97%;
        height: 110px;
    }

    .van-button--normal {
        width: 280px;
    }

    .van-uploader {
        /*left: 5%;*/
        margin-top: 5px;
    }

    .van-image {
        display: block;
        width: 100% !important;
        height: auto !important;
    }
    >>>.van-nav-bar__text{
        color: #fff;
    }
    >>>.van-icon::before{
        color: #fff;
    }
    >>>.van-nav-bar__title{
        color: #fff;
    }
</style>